import React from "react";

import AppContext from "../../context";
import styled from "styled-components";
import theme from "../../theme";
import GoodsCard from "../GoodsCard";

const GoodsList = styled.div`
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 50px;

  @media (max-width: ${theme.media.tab}) {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
  }
  @media (max-width: ${theme.media.mob}) {
    grid-template-columns: repeat(1, 1fr);
  }

  .card-wrapper {
    &:nth-child(1) {
      grid-column: span 2;
      .card {
        height: 100%;
        padding: 0;
      }
    }
    &:nth-child(2) {
      grid-column: span 2;
      .card {
        height: 100%;
        padding: 0;
      }
    }
    &:nth-child(7) {
      grid-column: span 2;
      grid-row: span 2;
      .card {
        height: 100%;
        padding: 0;
      }
    }
    &:nth-child(8) {
      grid-column: span 2;
      grid-row: span 1;
      .card {
        height: 100%;
        padding: 0;
      }
    }
    &:nth-child(13) {
      grid-column: span 2;
      grid-row: span 2;
      .card {
        height: 100%;
        padding: 0;
      }
    }
    &:nth-child(15) {
      grid-row: span 1;
      .card {
        height: 100%;
        padding: 0;
      }
    }
    &:nth-child(n) {
      @media (max-width: ${theme.media.tab}) {
        grid-column: span 1;
        grid-row: span 1;
        .card {
          height: auto;
          padding: 30% 0;
        }
      }
    }
  }
`;

function GoodsListComponent() {
  const { items } = React.useContext(AppContext);

  return (
    <GoodsList>
      {items.map((goods, index) => (
        <GoodsCard {...goods} key={index} />
      ))}
    </GoodsList>
  );
}

export default GoodsListComponent;
